import Authorization from "../user/auth.service";

const CountryService = {
    getList() {
        try {
            return Authorization.checkLogin(`/country`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            code: item.code,
                            coin: item.coin,
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/country`).then(res => {
                if (res.status == 200) {
                    const countries = []
                    for (let country of res.data) {
                        countries.push({
                            value: country.id,
                            text: country.alias
                        })
                    }
                    return countries
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    async getCountry(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/country/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdCountry(country_data, is_new = true) {
        try {
            //console.log(country_data)
            country_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/country/create', country_data, 'post'):
                await Authorization.checkLogin(`/country/upd/${country_data.id}`, country_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status, data } = await Authorization.checkLogin('/country/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default CountryService