<template>
    <section class="bg-white text-white pt-4">         

         <div class="bg-blue-180 pb-12">
            <h2 class="text-3xl py-6 text-white 
            md:text-6xl
            lg:text-4xl">- Varificar nueva cuenta -</h2>            

            <div class="container border-dashed border-2 rounded border-white p-5">              
                <div class="shadow-round bg-darkBlue text-xl px-10 py-8">
                    <h3 class="w-full text-left font-bold text-2xl pb-2">{{ message }}</h3>                    
                </div>
            </div>
         </div>
         
    </section>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import ClientService from "@/services/user/client.service";
import CountryService from "@/services/common/country.service";

export default {
    name:'VerifyUser',
    components: {
      ModelSelect
    },
    data() {
      return {
        message: '',        
      }
    },    
    created() {
        const token = this.$route.params.token        
        if(token) {           
            this.verifyUser(token)
        }            
    },  
    methods: {
        verifyUser(token) {            
            ClientService.verifyUser(token).then(c => {
                if(c.active) {
                    this.message = 'Su cuenta ha sido verificada satisfactoriamente'                
                } else {
                    this.message = 'Ha habido un problema a la hora de verificar su cuenta contactenos.'
                }
            })
        }
    }
}
</script>
